/* eslint-disable max-len */
import { canvas } from './ChoiceCanvas';
import { refresh } from './SupportFunction';
import { instrument } from './Instruments';

export default function frameManagement() {
  const mainCanvas = document.getElementById('myCanvas');


  mainCanvas.addEventListener('mousedown', () => {
    document.addEventListener('mouseup', function mouseUp() {
      const current = document.getElementById('current');
      const currentContext = current.getContext('2d');
      refresh(canvas.array, currentContext, 160 / canvas.array.length);


      instrument.frames[`${current.parentElement.id}`] = JSON.parse(JSON.stringify(canvas.array));
      document.removeEventListener('mouseup', mouseUp);
    });
  });

  mainCanvas.addEventListener('click', () => {
    const current = document.getElementById('current');
    const currentContext = current.getContext('2d');
    refresh(canvas.array, currentContext, 160 / canvas.array.length);


    instrument.frames[`${current.parentElement.id}`] = JSON.parse(JSON.stringify(canvas.array));
  });

  const addFrame = document.getElementById('addFrame');
  addFrame.addEventListener('click', () => {
    const frames = document.getElementById('main-frames');
    const current = document.getElementById('current');
    instrument.frames[current.parentElement.id] = JSON.parse(JSON.stringify(canvas.array));


    canvas.array = JSON.parse(JSON.stringify(canvas.arrayClean));

    const context = mainCanvas.getContext('2d');
    context.clearRect(
      0, 0, canvas.array.length * 5, canvas.array.length * 5,
    );
    if (current !== null) {
      current.removeAttribute('id');
    }


    const frame = document.createElement('div');
    frame.className = 'frame';
    const tmpKeys = Object.keys(instrument.frames);
    frame.id = `frame_${+tmpKeys[tmpKeys.length - 1].match(/\d+/) + 1}`;
    frames.appendChild(frame);
    instrument.frames[frame.id] = JSON.parse(JSON.stringify(canvas.array));

    const num = document.createElement('div');
    num.className = 'namber';
    num.innerHTML = `${+tmpKeys[tmpKeys.length - 1].match(/\d+/) + 1}`;
    frame.appendChild(num);


    const del = document.createElement('div');
    del.className = 'delete';
    del.innerHTML = 'X';
    frame.appendChild(del);


    const copy = document.createElement('div');
    copy.className = 'coppy';
    copy.innerHTML = '<img src="dist/assets/copy.png" alt="18">';
    frame.appendChild(copy);


    const move = document.createElement('div');
    move.className = 'move';
    move.innerHTML = '<img src="dist/assets/move-frame.png" alt="18" hidden>';
    frame.appendChild(move);


    const canv = document.createElement('canvas');
    canv.className = `canvas canvas_${+tmpKeys[tmpKeys.length - 1].match(/\d+/) + 1}`;
    canv.id = 'current';
    canv.width = 160;
    canv.height = 160;
    frame.appendChild(canv);
  });


  const frames = document.getElementById('main-frames');
  frames.addEventListener('click', (e) => {
    let tmpKeys = Object.keys(instrument.frames);
    let tmpValues = Object.values(instrument.frames);
    if (tmpKeys.length > 1) {
      if (e.target.className === 'delete') {
        delete instrument.frames[e.target.parentNode.id];


        if (e.target.parentElement.children[4].id === 'current') {
          frames.removeChild(e.target.parentNode);
          tmpKeys = Object.keys(instrument.frames);
          tmpValues = Object.values(instrument.frames);

          frames.children[tmpKeys.length].lastElementChild.setAttribute('id', 'current');


          const context = mainCanvas.getContext('2d');
          canvas.array = tmpValues[tmpValues.length - 1];

          refresh(canvas.array, context, 5);
        } else {
          frames.removeChild(e.target.parentNode);
        }
      }
    } else {
      canvas.array = JSON.parse(JSON.stringify(canvas.arrayClean));


      let context = mainCanvas.getContext('2d');
      context.clearRect(
        0, 0, canvas.array.length * 5, canvas.array.length * 5,
      );


      context = e.target.parentElement.children[4].getContext('2d');

      context.clearRect(
        0, 0, canvas.array.length * 5, canvas.array.length * 5,
      );
    }
  });


  const coppy = document.getElementById('coppy');
  coppy.addEventListener('click', (e) => {
    let tmpKeys = Object.keys(instrument.frames);


    const frame = document.createElement('div');
    frame.className = 'frame';
    tmpKeys = Object.keys(instrument.frames);
    frame.id = `frame_${+tmpKeys[tmpKeys.length - 1].match(/\d+/) + 1}`;
    frames.appendChild(frame);

    instrument.frames[frame.id] = JSON.parse(JSON.stringify(instrument.frames[e.target.parentElement.parentElement.id]));

    const num = document.createElement('div');
    num.className = 'namber';
    num.innerHTML = `${+tmpKeys[tmpKeys.length - 1].match(/\d+/) + 1}`;
    frame.appendChild(num);

    const del = document.createElement('div');
    del.className = 'delete';
    del.innerHTML = 'X';
    frame.appendChild(del);

    const copy = document.createElement('div');
    copy.className = 'coppy';
    copy.innerHTML = '<img src="dist/assets/copy.png" alt="18">';
    frame.appendChild(copy);

    const move = document.createElement('div');
    move.className = 'move';
    move.innerHTML = '<img src="dist/assets/move-frame.png" alt="18">';
    frame.appendChild(move);


    const canv = document.createElement('canvas');
    canv.className = `canvas canvas_${+tmpKeys[tmpKeys.length - 1].match(/\d+/) + 1}`;
    canv.width = 160;
    canv.height = 160;
    frame.appendChild(canv);
    const ctx = canv.getContext('2d');
    refresh(instrument.frames[frame.id], ctx, 4);
  });

  frames.addEventListener('click', (e) => {
    if (e.target.width === 160) {
      if (e.target.id !== 'current') {
        const current = document.getElementById('current');
        const context = mainCanvas.getContext('2d');

        canvas.array = JSON.parse(JSON.stringify(instrument.frames[e.target.parentElement.id]));
        context.clearRect(
          0, 0, canvas.array.length * 5, canvas.array.length * 5,
        );
        refresh(canvas.array, context, 5);
        current.removeAttribute('id');
        e.target.id = 'current';
      }
    }
  });
}
