/* eslint-disable prefer-const */
/* eslint-disable radix */
/* eslint-disable no-unused-vars */
/* eslint-disable func-names */
/* eslint-disable no-plusplus */

const canvas = {
  canvas: '?',
  array: '?',
};

const choiceCanvas = function () {
  function canvasCreation() {
    const center = document.getElementById('main-canvas');
    const canvasSizeTemp = document.getElementById('canvasSizeTemp');
    let mainCanvas;
    let array;
    let canvasSize;


    function arrayCreator(n) {
      array = new Array(n);
      for (let i = 0; i < n; i++) {
        array[i] = new Array(n);
        for (let j = 0; j < n; j++) {
          array[i][j] = { color: '?' };
        }
      }
    }


    canvasSizeTemp.addEventListener('click', (e) => {
      if (e.target.className === 'choice-canvas') {
        canvasSize = +e.target.innerHTML;
        center.removeChild(canvasSizeTemp);

        mainCanvas = document.getElementById('myCanvas');
        mainCanvas.width = canvasSize * 5;
        mainCanvas.style.width = `${canvasSize * 5}px`;
        mainCanvas.height = canvasSize * 5;
        mainCanvas.className = 'general-canvas';
        center.appendChild(mainCanvas);
        mainCanvas.style.opacity = '1';

        arrayCreator(canvasSize);
        canvas.canvas = mainCanvas;
        canvas.array = array;
        canvas.arrayClean = JSON.parse(JSON.stringify(array));
        const size = document.getElementById('canvas-size');
        size.innerHTML = ` [${canvas.array.length}x${canvas.array.length}]`;
      }
      const preview = document.getElementById('previewCanvas');


      const ctx = preview.getContext('2d');
      let i = 0;
      let previewArray;

      const fpsInput = document.getElementById('inputFps');
      const numberFps = document.getElementById('nuberFps');
      fpsInput.addEventListener('input', (event) => {
        const number = event.target.value;
        numberFps.innerHTML = `${number} FPS`;
      });
      fpsInput.addEventListener('change', (event) => {
        const number = event.target.value;
        numberFps.innerHTML = `${number} FPS`;
      });

      global.console.log(fpsInput.value);
      const timerId = setInterval(() => {
        const collection = document.getElementsByClassName('frame');
        previewArray = Array.from(collection);

        if (i < previewArray.length) {
          ctx.clearRect(0, 0, 240, 240);
          ctx.drawImage(previewArray[i].children[4], 0, 0);
          i++;
          if (i >= previewArray.length) {
            i = 0;
          }
        } else {
          i = 0;
        }
      }, (5000 / fpsInput.value));
    });
  }
  canvasCreation();
};

export default choiceCanvas;

export { canvas };
