/* eslint-disable no-undef */
/* eslint-disable func-names */


import { instrument } from './Instruments';

const keyboardShortcuts = function () {
  document.addEventListener('keypress', (e) => {
    const keyName = e.key;
    if (keyName === 'p' || keyName === 'P') {
      instrument.state = 'pen';
    } else if (keyName === 'v' || keyName === 'V') {
      instrument.state = 'mirrorVertical';
    } else if (keyName === 'e' || keyName === 'E') {
      instrument.state = 'eraser';
    } else if (keyName === 'c' || keyName === 'C') {
      instrument.state = 'color-picker';
    } else if (keyName === 'b' || keyName === 'B') {
      instrument.state = 'bucket-all';
    } else if (keyName === 'h' || keyName === 'H') {
      instrument.state = 'mirror';
    } else if (keyName === 'r' || keyName === 'R') {
      instrument.state = 'rectangle';
    }
  });

  const keyboard = document.getElementById('keyboard');
  const key = document.getElementById('key');
  keyboard.addEventListener('click', () => {
    if (key.style.display === 'none') key.style.display = 'block';
    else {
      key.style.display = 'none';
    }
  });
};

export default keyboardShortcuts;
